import React from 'react';
import Calendar from 'react-calendar';
import { Input, Button } from 'mdbreact';
import '../css/calendar.css';

class Calenda extends React.Component  {

    constructor(){
        super();
      
        this.state = {
          date: new Date(),
        }
      }

    
      onChange = date => this.setState({ date })

  render() {
    return(

      <div>


<div className="row">
    <div className="col-12 col-md-6" id="calendar">
    
    <Calendar className="mt-3 "
          onChange={this.onChange}
          value={this.state.date}
        />
    
    
    </div>
    <div className=" mt-4  col-12 col-md-6" id="Input2">
    

<Input label="Descanso Oficial"  filled disabled type="checkbox" id="checkbox3" />

{/* Filled-in checked disabled */}
<Input label="Faltas Programadas"     filled checked disabled type="checkbox" id="checkbox4" />
<hr></hr>  
<h6>Programa inasistencia</h6>  
<p><input type="date"></input></p>
<h6>Motivo</h6>  
<textarea  rows="5" cols="5" name='post' className="form-control" />
<Button color="secondary" type="submit">Confirmar</Button>
                   
    </div>
   
</div>

       
    
</div>




    );
  }
};

export default Calenda;